import React from 'react';
import { Box, Paper, Typography } from '@mui/material';
import './style.sass';

const TaskCard: React.FC = () => {
	return (
		<Paper elevation={3} sx={{ p: '3% 5%' }} className='taskCardContainer'>
			<Box className='taskCardContainerLeft'>
				<Typography className='taskCardTitle'>
					   
				</Typography>
				<Typography className='taskCardP'> , </Typography>
				<Typography className='taskCardP'> 20 , 11:50</Typography>
			</Box>
			<Box>
				<Typography className='taskCardPrice'>65 000 p</Typography>
				<Typography className='taskCardCustomer'></Typography>
				<Typography className='taskCardRewiews'> </Typography>
			</Box>
		</Paper>
	);
};

export default TaskCard;
