import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Box, Typography } from '@mui/material';
import './style.css';
import CustomInput from '../../Components/CustomInput/Index';
import CustomButton from '../../Components/CustomButton/Index';

const LoginPage: React.FC = () => {
	const router = useNavigate();

	const [email, setEmail] = useState('');
	const [password, setPassword] = useState('');
	const [check, setCheck] = useState(false);
	const [isError, setIsError] = useState(false);

	const handleLogIn = () => {
		//dispatch({ type: loginActions.LOGIN_REQUEST, payload: { 'email': email, 'password': password } });
		setEmail('');
		setPassword('');
	};

	const handleChangeEmail = (ev: React.ChangeEvent<HTMLInputElement>) => {
		const {
			target: { value },
		} = ev;
		setEmail(value);
	};

	const handleChangePassword = (ev: React.ChangeEvent<HTMLInputElement>) => {
		const {
			target: { value },
		} = ev;
		setPassword(value);
	};

	useEffect(() => {
		setIsError(false);
		(email === '' || password === '') && setIsError(true);
	}, [email, password]);

	return (
		<Box className='loginFormContainer'>
			<Typography className='actionName'></Typography>
			<CustomInput
				item={email}
				onChange={handleChangeEmail}
				check={check}
				placeholder={'  email'}
				label={'Email'}
			/>
			<CustomInput
				item={password}
				onChange={handleChangePassword}
				check={check}
				placeholder={'  '}
				label={''}
			/>
			<Typography
				className='forgotPasswordLink'
				onClick={() => router('/forgotpassword')}
			>
				 ?
			</Typography>
			<CustomButton
				text={''}
				onClick={() => {
					if (isError) setCheck(true);
					else handleLogIn();
				}}
			/>
			<Typography className='signUpLinkThin' onClick={() => router('/signup')}>
				 ?<span className='signUpLinkBold'></span>
			</Typography>
		</Box>
	);
};
export default LoginPage;
